import Vue from 'vue'
import VueRouter from 'vue-router'
import View from '@/views/View.vue'

Vue.use(VueRouter)

const routes = [
	{
		path: '/',
		component: View,
		name: 'viewlongread',
		props: { 
			is_admin: false 
		}
	}
]

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes
})

export default router
